import { ArtistModel } from '@/entities/artist/index.js';

export const getFilteredAlbumArtists = (artists: ArtistModel[] = []): ArtistModel[] => {
    const variousArtists = artists.filter((artist: ArtistModel) => !artist.composer);
    const nonspecificArtists = variousArtists.filter((artist: ArtistModel) => !artist.various);
    const composersArtists = artists.filter((artist: ArtistModel) => artist.composer && !artist.various);

    let filteredArtists: ArtistModel[] = [];

    if (nonspecificArtists.length > 0) {
        filteredArtists = nonspecificArtists;
    } else if (variousArtists.length > 0) {
        filteredArtists = variousArtists;
    }

    return filteredArtists.concat(composersArtists);
};
