'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useMemo, forwardRef, Ref } from 'react';

import { WithForwardRef } from '@yandex-music-int/sol/common/types/index.js';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption, Heading } from '@yandex-music-int/sol/components/Typography';

import { useStore, LayoutName, WithClassName, WithAria } from '@/shared/lib/index.js';
import { Link } from '@/shared/ui/index.js';

import styles from './PageHeaderBase.module.scss';

interface Props extends WithClassName, WithAria {
    coverVariant?: 'round' | 'square';
    coverUri?: string;
    entityName: string;
    title?: string;
    meta?: ReactElement;
    coverLink?: string;
    controls: ReactElement;
}

const COVER_SIZE = 300;

export const PageHeaderBaseComponent = observer(
    ({
        'aria-labelledby': ariaLabelledById,
        entityName,
        title,
        meta,
        controls,
        className,
        coverVariant = 'square',
        coverUri,
        coverLink,
        forwardRef: ref
    }: WithForwardRef<HTMLDivElement, Props>): ReactElement => {
        const { settings } = useStore();

        const isMobile = useMemo(() => {
            return settings.layout === LayoutName.Mobile;
        }, [settings.layout]);

        const isLongTitle = useMemo(() => {
            return title && title.length > 25;
        }, [title]);

        const titleSize = useMemo(() => {
            if (isMobile) {
                return 'm';
            } else if (isLongTitle) {
                return 'l';
            }

            return 'xxl';
        }, [isMobile, isLongTitle]);

        const cover = useMemo(() => {
            if (coverLink) {
                return (
                    <Link href={coverLink} className={clsx(styles.coverCell, styles.rewindLink)}>
                        <Image
                            fit="cover"
                            src={coverUri}
                            size={COVER_SIZE}
                            className={styles.coverImage}
                            withAvatarReplace
                        />
                    </Link>
                );
            }
            return (
                <Paper radius={coverVariant === 'round' ? 'round' : 'm'} className={styles.coverCell}>
                    <Image
                        fit="cover"
                        src={coverUri}
                        size={COVER_SIZE}
                        className={styles.coverImage}
                        withAvatarReplace
                    />
                </Paper>
            );
        }, [coverLink, coverUri, coverVariant]);

        return (
            <div className={clsx(styles.root, className)} ref={ref}>
                {cover}
                <div className={styles.content}>
                    <div className={styles.info}>
                        <Caption variant="div" type="text" size="m" weight="medium" className={styles.entityName}>
                            {entityName}
                        </Caption>

                        <Heading
                            variant="h2"
                            size={titleSize}
                            weight={isMobile || isLongTitle ? 'bold' : 'black'}
                            id={ariaLabelledById}
                            className={styles.title}
                        >
                            {title}
                        </Heading>

                        <div className={styles.meta}>{meta}</div>
                    </div>

                    <div className={styles.controls}>{controls}</div>
                </div>
            </div>
        );
    }
);

export const PageHeaderBase = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <PageHeaderBaseComponent forwardRef={ref} {...props} />
));
