'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useMemo, forwardRef, Ref } from 'react';
import { useIntl, FormattedMessage } from 'react-intl';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { WithForwardRef } from '@yandex-music-int/sol/common/types/index.js';
import { Button } from '@yandex-music-int/sol/components/Button';
import { CollapsableText } from '@yandex-music-int/sol/components/CollapsableText';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { PlaylistMetaModel } from '@/features/playlist/index.js';

import { useOnLikeClick } from '@/entities/playlist/index.js';

import {
    useStore,
    LayoutName,
    WithClassName,
    usePlayableContext,
    AnalyticsPage,
    AnalyticsBlock,
    useCreateFrom
} from '@/shared/lib/index.js';
import { PlayButton, LikeButton, useFuture } from '@/shared/ui/index.js';

import { convertLinksInDescription } from '../../../lib/convertLinksInDescription.js';
import { PageHeaderBase } from '../../PageHeaderBase/PageHeaderBase.js';

import styles from './PageHeaderPlaylist.module.scss';
import { UpdatedText } from './UpdatedText.js';

interface Props extends WithClassName {
    playlist: PlaylistMetaModel;
    isDisabled?: boolean;
}

export const PageHeaderPlaylistComponent = observer(
    ({ playlist, isDisabled, className, forwardRef: ref }: WithForwardRef<HTMLDivElement, Props>): ReactElement => {
        const from = useCreateFrom({ pageId: AnalyticsPage.PLAYLIST, blockId: AnalyticsBlock.PLAYLIST });
        const { settings } = useStore();
        const { isFuture } = useFuture();
        const { formatMessage, formatNumber } = useIntl();

        const handleOnLikeClick = useOnLikeClick(playlist);

        const { isPlaying, togglePlay } = usePlayableContext({
            playContextParams: {
                contextData: {
                    type: YaMusicContexts.Playlist,
                    meta: {
                        id: playlist.uuid
                    },
                    from
                },
                loadContextMeta: true
            }
        });

        const isMobile = useMemo(() => {
            return settings.layout === LayoutName.Mobile;
        }, [settings.layout]);

        const meta = useMemo(() => {
            return (
                <div className={styles.meta}>
                    <CollapsableText
                        lineClamp={2}
                        moreText={formatMessage({ id: 'interface-actions.read-full' })}
                        lessText={formatMessage({ id: 'interface-actions.collapse' })}
                        buttonClassName={styles.descriptionButton}
                    >
                        <Caption variant="span" className={styles.description} type="text" size="m" weight="medium">
                            {convertLinksInDescription(playlist.description, styles.descriptionLink)}
                        </Caption>
                    </CollapsableText>

                    <UpdatedText className={styles.updatedText} playlist={playlist} />
                </div>
            );
            // eslint-disable-next-line react-hooks/exhaustive-deps
        }, [formatMessage, playlist, playlist.description]);

        const likesComponent = useMemo(() => {
            if (playlist.actualLikesCount && playlist.actualLikesCount > 0) {
                return (
                    <Caption variant="span" type="controls" size="s" weight="medium">
                        {playlist.actualLikesCount && formatNumber(playlist.actualLikesCount)}
                    </Caption>
                );
            }
            return null;
        }, [playlist.actualLikesCount, formatNumber]);

        const controls = useMemo(() => {
            return isMobile ? (
                <div className={clsx(styles.controls, styles.controls_mobile)}>
                    {isFuture && (
                        <Button
                            size="l"
                            radius="round"
                            aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                            icon={<Icon size="m" variant="more" />}
                        />
                    )}

                    <PlayButton
                        className={clsx(styles.playButton, styles.playButton_mobile)}
                        color="primary"
                        buttonVariant="default"
                        iconSize="xxl"
                        isPlaying={isPlaying}
                        variant="filled"
                        onClick={togglePlay}
                        withRipple
                        disabled={isDisabled}
                    />

                    {!playlist.isFavouritePlaylist && (
                        <LikeButton
                            isLiked={playlist.isLiked}
                            onClick={handleOnLikeClick}
                            withRipple
                            variant="default"
                            size="l"
                            iconSize="m"
                            iconClassName={styles.likeIcon}
                        />
                    )}
                </div>
            ) : (
                <div className={clsx(styles.controls, styles.controls_desktop)}>
                    <PlayButton
                        withRipple
                        buttonVariant="default"
                        radius="xxxl"
                        size="s"
                        color="primary"
                        iconSize="xxs"
                        isPlaying={isPlaying}
                        onClick={togglePlay}
                        variant="default"
                        disabled={isDisabled}
                    >
                        <FormattedMessage id="player-actions.listen" />
                    </PlayButton>

                    {!playlist.isFavouritePlaylist && (
                        <LikeButton
                            isLiked={playlist.isLiked}
                            onClick={handleOnLikeClick}
                            withRipple
                            iconSize="xxs"
                            size="s"
                            variant="default"
                            iconClassName={styles.likeIcon}
                        >
                            {likesComponent}
                        </LikeButton>
                    )}

                    {isFuture && (
                        <Button
                            size="s"
                            radius="round"
                            aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                            icon={<Icon size="xxs" variant="more" />}
                        />
                    )}

                    {isFuture && (
                        <Button
                            size="s"
                            radius="round"
                            aria-label={formatMessage({ id: 'interface-actions.pin-in-menu' })}
                            icon={<Icon size="xxs" variant="favorites" />}
                        />
                    )}
                </div>
            );
        }, [
            isMobile,
            isFuture,
            formatMessage,
            isPlaying,
            togglePlay,
            isDisabled,
            playlist.isFavouritePlaylist,
            playlist.isLiked,
            handleOnLikeClick,
            likesComponent
        ]);

        return (
            <PageHeaderBase
                className={className}
                controls={controls}
                meta={meta}
                entityName={formatMessage({ id: 'entity-names.playlist' })}
                title={playlist.title}
                coverVariant="square"
                coverUri={playlist.coverUri}
                ref={ref}
            />
        );
    }
);

export const PageHeaderPlaylist = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <PageHeaderPlaylistComponent forwardRef={ref} {...props} />
));
