'use client';

import { observer } from 'mobx-react-lite';
import { ReactElement, useCallback, useMemo } from 'react';
import { useIntl } from 'react-intl';

import { OwnerSex } from '@yandex-music-int/resource-collection';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { PlaylistMetaModel } from '@/features/playlist/index.js';

import { WithClassName, getDateType, DateType } from '@/shared/lib/index.js';

interface Props extends WithClassName {
    playlist: PlaylistMetaModel;
}

export const UpdatedText = observer(({ playlist, className }: Props): ReactElement => {
    const { formatMessage } = useIntl();

    const textMadeFor = useCallback(
        (dateType: DateType, values: Parameters<typeof formatMessage>[1]) => {
            switch (dateType) {
                case DateType.TODAY:
                    return formatMessage({ id: 'interface-actions.playlist-made-for-today' }, values);
                case DateType.YESTERDAY:
                    return formatMessage({ id: 'interface-actions.playlist-made-for-yesterday' }, values);
                case DateType.DATE_WITH_YEAR:
                    return formatMessage({ id: 'interface-actions.playlist-made-for-date-with-year' }, values);
                default:
                    return formatMessage({ id: 'interface-actions.playlist-made-for-date' }, values);
            }
        },
        [formatMessage]
    );

    const textUpdatedByMale = useCallback(
        (dateType: DateType, values: Parameters<typeof formatMessage>[1]) => {
            switch (dateType) {
                case DateType.TODAY:
                    return formatMessage({ id: 'interface-actions.he-updated-playlist-today' }, values);
                case DateType.YESTERDAY:
                    return formatMessage({ id: 'interface-actions.he-updated-playlist-yesterday' }, values);
                case DateType.DATE_WITH_YEAR:
                    return formatMessage({ id: 'interface-actions.he-updated-playlist-date-with-year' }, values);
                default:
                    return formatMessage({ id: 'interface-actions.he-updated-playlist-date' }, values);
            }
        },
        [formatMessage]
    );

    const textUpdatedByFemale = useCallback(
        (dateType: DateType, values: Parameters<typeof formatMessage>[1]) => {
            switch (dateType) {
                case DateType.TODAY:
                    return formatMessage({ id: 'interface-actions.she-updated-playlist-today' }, values);
                case DateType.YESTERDAY:
                    return formatMessage({ id: 'interface-actions.she-updated-playlist-yesterday' }, values);
                case DateType.DATE_WITH_YEAR:
                    return formatMessage({ id: 'interface-actions.she-updated-playlist-date-with-year' }, values);
                default:
                    return formatMessage({ id: 'interface-actions.she-updated-playlist-date' }, values);
            }
        },
        [formatMessage]
    );

    const text = useMemo(() => {
        const updateDate = new Date(playlist.modified);
        const dateType = getDateType(updateDate);

        if (playlist.madeFor) {
            const playlistReceiver = playlist?.madeFor?.caseForms?.genitive || playlist?.madeFor?.userInfo?.login || '';
            const values = {
                playlistReceiver,
                updateDate
            };

            return textMadeFor(dateType, values);
        } else if (playlist.isFavouritePlaylist) {
            return playlist.owner?.name || '';
        }

        const values = {
            updateDate: new Date(playlist.modified),
            ownerName: playlist.owner?.name || ''
        };

        if (playlist.owner?.sex === OwnerSex.FEMALE) {
            return textUpdatedByFemale(dateType, values);
        }

        return textUpdatedByMale(dateType, values);
    }, [playlist, textUpdatedByMale, textMadeFor, textUpdatedByFemale]);

    return (
        <Caption variant="span" className={className} type="text" size="m" weight="medium" lineClamp={1}>
            {text}
        </Caption>
    );
});
