import { ReactNode } from 'react';

import { Link } from '@/shared/ui/index.js';

const LINK_RE = /\[([^([\])]+)\]\(([^(()\s)]+)\)/g;
const LINK_RE_WITHOUT_GROUP = /\[[^([\])]+\]\([^(()\s)]+\)/;

export const convertLinksInDescription = (description?: string, linkClassName?: string): ReactNode[] => {
    if (!description) {
        return [];
    }

    const parsedLinks = [...description.matchAll(LINK_RE)];
    const textsBetween = description.split(LINK_RE_WITHOUT_GROUP);

    /*
        description всегда парсится следующим образом:
        textsBetween[0] + parsedLinks[0] + textsBetween[1] + parsedLinks[1] + ... + textsBetween[n]
    */
    return textsBetween.reduce((convertedDescription: ReactNode[], textBetween: string, index: number) => {
        convertedDescription.push(textBetween);

        const { 1: linkText, 2: linkUrl } = parsedLinks[index] || [];

        if (linkText && linkUrl) {
            convertedDescription.push(
                <Link href={linkUrl} className={linkClassName}>
                    {linkText}
                </Link>
            );
        }

        return convertedDescription;
    }, []);
};
