'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useMemo, forwardRef, Ref } from 'react';
import { useIntl, FormattedMessage } from 'react-intl';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { WithForwardRef } from '@yandex-music-int/sol/common/types/index.js';
import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { ArtistMetaModel } from '@/features/artist/index.js';

import { useOnLikeClick } from '@/entities/artist/index.js';

import {
    AnalyticsBlock,
    AnalyticsPage,
    LayoutName,
    useCreateFrom,
    usePlayableContext,
    useStore,
    WithClassName
} from '@/shared/lib/index.js';
import { LikeButton, PlayButton, useFuture } from '@/shared/ui/index.js';

import { PageHeaderBase } from '../../PageHeaderBase/PageHeaderBase.js';

import styles from './PageHeaderArtist.module.scss';

interface Props extends WithClassName {
    artistMeta?: ArtistMetaModel;
    coverLink?: string;
}

export const PageHeaderArtistComponent = observer(
    ({ artistMeta, coverLink, className, forwardRef: ref }: WithForwardRef<HTMLDivElement, Props>): ReactElement => {
        const from = useCreateFrom({ pageId: AnalyticsPage.ARTIST, blockId: AnalyticsBlock.ARTIST });
        const { settings } = useStore();
        const { isFuture } = useFuture();
        const handleLikeClick = useOnLikeClick(artistMeta?.artist);
        const { formatMessage } = useIntl();
        const artistType = useMemo(() => {
            return artistMeta?.artist.composer
                ? formatMessage({ id: 'entity-names.composer' })
                : formatMessage({ id: 'entity-names.singer' });
        }, [formatMessage, artistMeta?.artist.composer]);

        const isMobile = useMemo(() => {
            return settings.layout === LayoutName.Mobile;
        }, [settings.layout]);

        const { isPlaying, togglePlay } = usePlayableContext({
            playContextParams: {
                contextData: {
                    type: YaMusicContexts.Artist,
                    meta: {
                        id: Number(artistMeta?.artist.id)
                    },
                    from
                },
                loadContextMeta: true
            }
        });

        const controls = useMemo(() => {
            return isMobile ? (
                <div className={clsx(styles.controls, styles.controls_mobile)}>
                    {isFuture && (
                        <Button
                            size="l"
                            radius="round"
                            aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                            icon={<Icon size="m" variant="more" />}
                        />
                    )}

                    <PlayButton
                        className={clsx(styles.playButton, styles.playButton_mobile)}
                        color="primary"
                        buttonVariant="default"
                        iconSize="xxl"
                        isPlaying={isPlaying}
                        variant="filled"
                        onClick={togglePlay}
                        disabled={!artistMeta?.artist?.isAvailableForPlaying}
                        withRipple
                    />

                    <LikeButton
                        isLiked={artistMeta?.artist.isLiked}
                        onClick={handleLikeClick}
                        withRipple
                        variant="default"
                        size="l"
                        iconSize="m"
                        iconClassName={styles.likeIcon}
                    />
                </div>
            ) : (
                <div className={clsx(styles.controls, styles.controls_desktop)}>
                    <PlayButton
                        withRipple
                        buttonVariant="default"
                        radius="xxxl"
                        size="s"
                        color="primary"
                        iconSize="xxs"
                        isPlaying={isPlaying}
                        disabled={!artistMeta?.artist?.isAvailableForPlaying}
                        onClick={togglePlay}
                        variant="default"
                    >
                        <FormattedMessage id="player-actions.listen" />
                    </PlayButton>

                    <LikeButton
                        isLiked={artistMeta?.artist.isLiked}
                        onClick={handleLikeClick}
                        withRipple
                        iconSize="xxs"
                        size="s"
                        variant="default"
                        iconClassName={styles.likeIcon}
                    />

                    {isFuture && (
                        <Button
                            size="s"
                            radius="round"
                            aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                            icon={<Icon size="xxs" variant="more" />}
                        />
                    )}

                    {isFuture && (
                        <Button
                            size="s"
                            radius="round"
                            aria-label={formatMessage({ id: 'interface-actions.pin-in-menu' })}
                            icon={<Icon size="xxs" variant="favorites" />}
                        />
                    )}
                </div>
            );
        }, [
            isMobile,
            isFuture,
            formatMessage,
            isPlaying,
            togglePlay,
            artistMeta?.artist?.isAvailableForPlaying,
            artistMeta?.artist.isLiked,
            handleLikeClick
        ]);

        const meta = useMemo(() => {
            return (
                <div className={styles.meta}>
                    {artistMeta?.lastMonthListeners && (
                        <div className={styles.listeners}>
                            <Icon variant="users" size="xxxs" />
                            <Caption type="text" size="m" weight="medium" variant="span">
                                <FormattedMessage
                                    id="entity-names.listeners-per-month"
                                    values={{ counter: artistMeta.lastMonthListeners }}
                                />
                            </Caption>
                        </div>
                    )}
                </div>
            );
        }, [artistMeta?.lastMonthListeners]);

        return (
            <PageHeaderBase
                className={clsx(styles.root, className)}
                entityName={artistType}
                title={artistMeta?.artist?.name || ''}
                meta={meta}
                controls={controls}
                coverVariant="round"
                coverUri={artistMeta?.artist?.coverUri}
                coverLink={coverLink}
                ref={ref}
            />
        );
    }
);

export const PageHeaderArtist = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <PageHeaderArtistComponent forwardRef={ref} {...props} />
));
