'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useMemo, forwardRef, Ref } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';

import { YaMusicContexts } from '@yandex-music-int/player-contexts';
import { WithForwardRef } from '@yandex-music-int/sol/common/types/index.js';
import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { AlbumWithArtistsModel } from '@/features/album/index.js';

import { getAlbumTypeName, useOnLikeClick } from '@/entities/album/index.js';
import { SeparatedArtists } from '@/entities/artist/index.js';

import {
    useStore,
    LayoutName,
    WithClassName,
    WithAria,
    usePlayableContext,
    AnalyticsPage,
    AnalyticsBlock,
    useCreateFrom
} from '@/shared/lib/index.js';
import { PlayButton, LikeButton, useFuture } from '@/shared/ui/index.js';

import { getFilteredAlbumArtists } from '../../../lib/getFilteredAlbumArtists.js';
import { PageHeaderBase } from '../../PageHeaderBase/PageHeaderBase.js';

import styles from './PageHeaderAlbum.module.scss';

interface Props extends WithClassName, WithAria {
    album: AlbumWithArtistsModel;
}

export const PageHeaderAlbumComponent = observer(
    ({
        album,
        className,
        'aria-labelledby': ariaLabelledById,
        forwardRef: ref
    }: WithForwardRef<HTMLDivElement, Props>): ReactElement => {
        const from = useCreateFrom({ pageId: AnalyticsPage.ALBUM, blockId: AnalyticsBlock.ALBUM });
        const { isFuture } = useFuture();
        const { settings } = useStore();
        const handleOnLikeClick = useOnLikeClick(album);
        const { formatMessage } = useIntl();
        const entityName = formatMessage({ id: getAlbumTypeName(album.type) });
        const isMobile = useMemo(() => {
            return settings.layout === LayoutName.Mobile;
        }, [settings.layout]);

        const artists = useMemo(() => getFilteredAlbumArtists(album.artists), [album.artists]);
        const shouldShowArtistCover = useMemo(
            () => artists?.length === 1 && !artists[0]?.decomposed && !artists[0]?.various,
            [artists]
        );

        const meta = (
            <div className={styles.meta}>
                {shouldShowArtistCover && (
                    <Paper radius="round" className={styles.artistCover}>
                        <Image src={artists[0]?.coverUri} size={30} withAvatarReplace />
                    </Paper>
                )}

                <SeparatedArtists
                    artists={artists}
                    lineClamp={isMobile ? 1 : undefined}
                    className={styles.artists}
                    spoilerClassName={styles.artistsSpoiler}
                    visibleArtistsCount={isMobile ? undefined : 2}
                    linkClassName={styles.artistLink}
                    captionClassName={styles.artistLabel}
                />

                {album.year && (
                    <Caption
                        variant="div"
                        type="text"
                        size="m"
                        weight="medium"
                        className={clsx(styles.year, { [styles.year_dot]: artists.length > 0 })}
                    >
                        {album.year}
                    </Caption>
                )}
            </div>
        );

        const { isPlaying, togglePlay } = usePlayableContext({
            playContextParams: {
                contextData: {
                    type: YaMusicContexts.Album,
                    meta: {
                        id: album.id
                    },
                    from
                },
                loadContextMeta: true
            }
        });

        const controls = useMemo(() => {
            return isMobile ? (
                <div className={clsx(styles.controls, styles.controls_mobile)}>
                    {isFuture && (
                        <Button
                            size="l"
                            radius="round"
                            aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                            icon={<Icon size="m" variant="more" />}
                        />
                    )}

                    <PlayButton
                        className={clsx(styles.playButton, styles.playButton_mobile)}
                        color="primary"
                        buttonVariant="default"
                        iconSize="xxl"
                        isPlaying={isPlaying}
                        onClick={togglePlay}
                        variant="filled"
                        disabled={!album.isAvailable}
                    />

                    <LikeButton
                        isLiked={album.isLiked}
                        onClick={handleOnLikeClick}
                        variant="default"
                        size="l"
                        iconSize="m"
                        iconClassName={styles.likeIcon}
                    />
                </div>
            ) : (
                <div className={clsx(styles.controls, styles.controls_desktop)}>
                    <PlayButton
                        buttonVariant="default"
                        radius="xxxl"
                        size="s"
                        color="primary"
                        iconSize="xxs"
                        isPlaying={isPlaying}
                        onClick={togglePlay}
                        variant="default"
                        disabled={!album.isAvailable}
                    >
                        <FormattedMessage id="player-actions.listen" />
                    </PlayButton>

                    <LikeButton
                        isLiked={album.isLiked}
                        onClick={handleOnLikeClick}
                        iconSize="xxs"
                        size="s"
                        variant="default"
                        iconClassName={styles.likeIcon}
                    />

                    {isFuture && (
                        <Button
                            size="s"
                            radius="round"
                            aria-label={formatMessage({ id: 'interface-actions.context-menu' })}
                            icon={<Icon size="xxs" variant="more" />}
                        />
                    )}

                    {isFuture && (
                        <Button
                            size="s"
                            radius="round"
                            aria-label={formatMessage({ id: 'interface-actions.pin-in-menu' })}
                            icon={<Icon size="xxs" variant="favorites" />}
                        />
                    )}
                </div>
            );
        }, [
            isMobile,
            isFuture,
            formatMessage,
            isPlaying,
            togglePlay,
            album.isAvailable,
            album.isLiked,
            handleOnLikeClick
        ]);

        return (
            <PageHeaderBase
                className={className}
                controls={controls}
                meta={meta}
                entityName={entityName}
                title={album.title}
                coverVariant="square"
                coverUri={album.coverUri}
                aria-labelledby={ariaLabelledById}
                ref={ref}
            />
        );
    }
);

export const PageHeaderAlbum = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <PageHeaderAlbumComponent forwardRef={ref} {...props} />
));
