'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useMemo } from 'react';

import { Shimmer, ShimmerProps } from '@yandex-music-int/sol/components/Shimmer';

import { useStore, LayoutName, WithClassName } from '@/shared/lib/index.js';

import styles from './PageHeaderShimmer.module.scss';

interface Props extends WithClassName {
    coverRadius?: ShimmerProps['radius'];
}

export const PageHeaderShimmer = observer(({ className, coverRadius = 'm' }: Props): ReactElement => {
    const { settings } = useStore();

    const controls = useMemo(() => {
        return settings.layout === LayoutName.Mobile ? (
            <div className={styles.controls}>
                <Shimmer className={styles.button} radius="round" />
                <Shimmer className={styles.button} radius="round" />
                <Shimmer className={styles.button} radius="round" />
            </div>
        ) : (
            <div className={styles.controls}>
                <Shimmer className={styles.desktopPlayButton} />
                <Shimmer className={styles.button} radius="round" />
                <Shimmer className={styles.button} radius="round" />
                <Shimmer className={styles.button} radius="round" />
            </div>
        );
    }, [settings.layout]);

    return (
        <div className={clsx(styles.root, className)}>
            <Shimmer className={styles.cover} radius={coverRadius} />
            <div className={styles.content}>
                <div className={styles.info}>
                    <Shimmer className={styles.entityName} radius="s" />
                    <Shimmer className={styles.title} radius="xl" />
                    <Shimmer className={styles.meta} radius="s" />
                </div>
                {controls}
            </div>
        </div>
    );
});
